

import { actions } from "./store"


const defaultStore = {
    items: [],
    currentAnime: {},
    isError: false
}

export let animeReducer = (state = defaultStore, action) =>{

switch (action.type){
    
    case actions.searchAnime:
        return{...state, items: action.items}
        
    case actions.setCurrAnime:
        return{...state, currentAnime: action.currAnime}
        
    case actions.resetAnime:
        return {...state, items: [], currentAnime: {}}

    default:
        return state

}
}





